.school_news {
  width: 100%;
  height: 300px;
  position: relative;
  display: flex;
  justify-content: start;
  align-items: end;
  margin: 20px 0px;
  cursor: pointer;
  overflow: hidden;
}
.school_news .school_news_opacity {
  width: 100%;
  height: 100%;
  opacity: 1;
  position: absolute;
  top: 0;
  z-index: 1;
  background: rgba(0, 0, 0, 0.767);
  transition: 0.5s;
}
.school_news:hover .school_news_opacity {
  opacity: 0;
  transition: 0.5s;
  position: absolute;
  top: 0;
}
.school_news p {
  margin-left: 30px;
  margin-bottom: 60px;
  z-index: 1;
  color: white;
  max-width: 200px;
  transition: 0.5s;
  opacity: 1;
}
.school_news h4 {
  color: white;
  font-size: 18px;
  position: absolute;
  top: 60px;
  right: 30px;
  z-index: 1;
  transition: 0.5s;
  opacity: 1;
}
.school_news:hover p {
  transition: 0.5s;
  opacity:0;
}
.school_news:hover h4 {
  transition: 0.5s;
  opacity:0;
}
.school_news img {
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.school_news:hover img {
  transition: 0.4s;
  transform: scale(1.05);
}
.school_news h2 {
  background: blue;
  color: white;
  position: absolute;
  font-size: 20px;
  padding: 0 10px;
  top: 0;
  left: 0;
  z-index: 1;
  height: 40px;
  display: flex;
  align-items: center;
font-weight: 500;
width: 100%;
}
.school_news .date_icon {
  color: white;
  font-size: 18px;
