.chat .chat-search-box {
    border-radius: 3px 0 0 0;
    padding: .75rem 1rem;
}

.chat .chat-search-box .input-group .form-control {
    border-radius: 2px 0 0 2px;
    border-right: 0;
}

.chat .chat-search-box .input-group .form-control:focus {
    border-right: 0;
}

.chat .chat-search-box .input-group .input-group-btn .btn {
    border-radius: 0 2px 2px 0;
    margin: 0;
}

.chat .chat-search-box .input-group .input-group-btn .btn i {
    font-size: 1.2rem;
    line-height: 100%;
    vertical-align: middle;
}

 @media (max-width: 767px) {
    .chat .chat-search-box {
        display: none;
    }
}


/************************************************
	************************************************
									Users Container
	************************************************
************************************************/

.chat .users-container {
    position: relative;
    padding: 1rem 0;
    border-right: 1px solid #e6ecf3;
    height: 100%;
    display: flex;
    flex-direction: column;
}


/************************************************
	************************************************
											Users
	************************************************
************************************************/

.chat .users {
    padding: 0;
}

.chat .users .person {
    position: relative;
    width: 100%;
    padding: 10px 1rem;
    cursor: pointer;
    border-bottom: 1px solid #f0f4f8;
}

.chat .users .person:hover {
    background-color: #ffffff;
    /* Fallback Color */
    /* Saf4+, Chrome */
    /* Chrome 10+, Saf5.1+, iOS 5+ */
    /* FF3.6 */
    /* IE10 */
    /* Opera 11.10+ */
    background-image: linear-gradient(right, #e9eff5, #ffffff);
}

.chat .users .person.active-user {
    background-color: #ffffff;
    /* Fallback Color */
    /* Saf4+, Chrome */
    /* Chrome 10+, Saf5.1+, iOS 5+ */
    /* FF3.6 */
    /* IE10 */
    /* Opera 11.10+ */
    background-image: linear-gradient(right, #f7f9fb, #ffffff);
}

.chat .users .person:last-child {
    border-bottom: 0;
}

.chat .users .person .user {
    display: inline-block;
    position: relative;
    margin-right: 10px;
}

.chat .users .person .user img {
    width: 48px;
    height: 48px;
    border-radius: 50px;
}

.chat .users .person .user .status {
    width: 10px;
    height: 10px;
    border-radius: 100px;
    background: #e6ecf3;
    position: absolute;
    top: 0;
    right: 0;
}

.chat .users .person .user .status.online {
    background: #9ec94a;
}

.chat .users .person .user .status.offline {
    background: #c4d2e2;
}

.chat .users .person .user .status.away {
    background: #f9be52;
}

.chat .users .person .user .status.busy {
    background: #fd7274;
}

.chat .users .person p.name-time {
    font-weight: 600;
    font-size: .85rem;
    display: inline-block;
}

.chat .users .person p.name-time .time {
    font-weight: 400;
    font-size: .7rem;
    text-align: right;
    color: #8796af;
}

 @media (max-width: 767px) {
    .chat .users .person .user img {
        width: 30px;
        height: 30px;
    }
    .chat .users .person p.name-time {
        display: none;
    }
    .chat .users .person p.name-time .time {
        display: none;
    }
}


/************************************************
	************************************************
									Chat right side
	************************************************
************************************************/

.chat .selected-user {
    width: 100%;
    padding: 0 15px;
    min-height: 64px;
    line-height: 64px;
    border-bottom: 1px solid #e6ecf3;
    border-radius: 0 3px 0 0;
}

.chat .selected-user span {
    line-height: 100%;
}

.chat .selected-user span.name {
    font-weight: 700;
}

.chat .chat-container {
    position: relative;
    padding: 1rem;
}

.chat-container li.chat-left,
.chat .chat-container li.chat-right {
    display: flex;
    flex: 1 1;
    flex-direction: row;
    margin-bottom: 40px;
}

.chat .chat-container li img {
    width: 48px;
    height: 48px;
    border-radius: 30px;
}

.chat .chat-container li .chat-avatar {
    margin-right: 20px;
}

.chat .chat-container li.chat-right {
    justify-content: flex-end;
}

.chat .chat-container li.chat-right > .chat-avatar {
    margin-left: 20px;
    margin-right: 0;
}

.chat .chat-container li .chat-name {
    font-size: .75rem;
    color: #999999;
    text-align: center;
}

.chat .chat-container li .chat-text {
    padding: .4rem 1rem;
    border-radius: 4px;
    background: #ffffff;
    font-weight: 300;
    line-height: 150%;
    position: relative;
}

.chat .chat-container li .chat-text:before {
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    top: 10px;
    left: -20px;
    border: 10px solid;
    border-color: transparent #ffffff transparent transparent;
}

.chat .chat-container li.chat-right > .chat-text {
    text-align: right;
}

.chat .chat-container li.chat-right > .chat-text:before {
    right: -20px;
    border-color: transparent transparent transparent #ffffff;
    left: inherit;
}

.chat .chat-container li .chat-hour {
    padding: 0;
    margin-bottom: 10px;
    font-size: .75rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: 0 0 0 15px;
}

.chat .chat-container li .chat-hour > span {
    font-size: 16px;
    color: #9ec94a;
}

.chat .chat-container li.chat-right > .chat-hour {
    margin: 0 15px 0 0;
}

@media (max-width: 767px) {
    .chat-container li.chat-left,
    .chat .chat-container li.chat-right {
        flex-direction: column;
        margin-bottom: 30px;
    }
    .chat .chat-container li img {
        width: 32px;
        height: 32px;
    }
    .chat .chat-container li.chat-left .chat-avatar {
        margin: 0 0 5px 0;
        display: flex;
        align-items: center;
    }
    .chat .chat-container li.chat-left .chat-hour {
        justify-content: flex-end;
    }
    .chat .chat-container li.chat-left .chat-name {
        margin-left: 5px;
    }
    .chat .chat-container li.chat-right .chat-avatar {
        order: -1;
        margin: 0 0 5px 0;
        align-items: center;
        display: flex;
        justify-content: right;
        flex-direction: row-reverse;
    }
    .chat .chat-container li.chat-right .chat-hour {
        justify-content: flex-start;
        order: 2;
    }
    .chat .chat-container li.chat-right .chat-name {
        margin-right: 5px;
    }
    .chat .chat-container li .chat-text {
        font-size: .8rem;
    }
}

.chat .chat-form {
    padding: 15px;
    width: 100%;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ffffff;
    border-top: 1px solid white;
}

.chat ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}
.chat .card {
    border: 0;
    background: #f4f5fb;
    border-radius: 2px;
    margin-bottom: 2rem;
    box-shadow: none;
}
